.SliderContainer{
    position: absolute;

}

.SliderCard{
    position: absolute;

    cursor: -webkit-grab;
    pointer-events: auto;

    min-width: 900px;
    max-width: 1200px;
    min-height: 600px;
    max-height: 800px;

    width: 1000px;
    height: 600px;
    background:coral;

    transform-origin: 50%, 50%;

    top: 50%;
    left: 50%;
}

.SliderCard a.gameName{
    position: absolute;
    pointer-events: none;

    font-size: 30px;

    left: 10%;
    top: 15%;
}

.SliderCard a.context{
    position: absolute;
    pointer-events: none;

    font-size: 20px;

    left: 10%;
    top: 30%;
}

.SliderCard a.icon{
    position: absolute;

    width: 100px;
    height: 100px;

    left: 80%;
    top: 70%;
}

.SliderCard img{
    width: 100px;
    height: 100px;

    left: auto;
    top: auto;
}