.topnav {
    max-width: 1920px;
    min-width: 1080px;

    width: auto;
    height: 50px;
    background-color: #333;
    overflow: hidden;
  }
  
  .topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .topnav a.leftAtt{
    margin-left: 30px;
  }
  
  .topnav a.Youtube{
    float: right;
    color: #f31515;
  }
  
  .topnav a.Instagram{
    float: right;
    margin-right: 20px;
  }
  
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  .topnav a.active {
    background-color: #4CAF50;
    color: white;
  }